:root {
  /* Color Theme Swatches in Hex */
  --Teal-and-Gray-B: #004a8a;
  --Teal-and-Gray-R: #616801;
  --Teal-and-Gray-1: #00747c;
  --Teal-and-Gray-2: #00bbc9;
  --Teal-and-Gray-3: #cacaca;
  --Teal-and-Gray-4: #878787;
  --Teal-and-Gray-5: #202022;

  /* colors */
  --color-white: #fff;
  --color-smoke-10: #fefefe;
  --color-smoke-30: #fafafa;
  --color-smoke-40: #f6f8fa;
  --color-smoke-45: #afb8c133;
  --color-smoke-50: #f5f5f5;
  --color-smoke-70: #f0f0f0;
  --color-smoke-90: #e1e1e1;
  --color-gray-10: #c1c1c1;
  --color-gray-20: #545454;
  --color-gray-30: #8e8e8e;
  --color-gray-50: #808080;
  --color-gray-70: #5d5d5d;
  --color-gray-red: #7f7979;
  --color-jet-20: #4a4a4a;
  --color-jet-30: #424242;
  --color-jet-50: #333;
  --color-jet-70: #222;
  --color-jet-80: #0c0911;
  --color-very-dark-blue: #24292f;
  --color-black: #000;
  --color-Turquoise-10: #39adeb;
  --color-Turquoise-20: #77dae7;
  --color-Turquoise-30: #68c292;
  --color-Turquoise-40: #e7dba5;
  --color-Turquoise-50: #b4e8ad;
  --color-Turquoise-60: #c2a5a8;
  --color-very-dark-cyan: #1e4e51;
  --color-light-grayish-green: #e9eae4;
  --color-keyword-function: #333;
  /* fonts */
  --rem-base: 18; /* used to compute rem value from desired pixel value (e.g., calc(18 / var(--rem-base) * 1rem) = 18px) */
  --body-font-size: 1.0625em; /* 17px */
  --body-font-size--desktop: 1.125em; /* 18px */
  --body-font-size--print: 0.9375em; /* 15px */
  --body-line-height: 1.15;
  --body-font-color: var(--color-jet-70);
  /* Native font stack */
  --body-font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", Arial, sans-serif;
  --body-font-weight-bold: 500;
  --monospace-font-family: ui-monospace, Menlo, Monaco, "Cascadia Mono", "Segoe UI Mono", "Roboto Mono", "Oxygen Mono", "Ubuntu Monospace", "Source Code Pro","Fira Mono", "Droid Sans Mono", "Courier New", monospace;
  --monospace-font-weight-bold: 500;
  /* base */
  --body-background: var(--color-white);
  --panel-background: var(--color-smoke-30); /* var(--color-smoke-30); */
  --panel-border-color: var(--color-smoke-90);
  --scrollbar-thumb-color: var(--Teal-and-Gray-5);
  /* navbar */
  --navbar-background: var(--Teal-and-Gray-1); /* var(--color-jet-80); */
  --navbar-font-color: var(--color-white);
  --navbar-hover-background: var(--color-very-dark-cyan);
  --navbar-button-background: var(--color-white);
  --navbar-button-border-color: var(--panel-border-color);
  --navbar-button-font-color: var(--body-font-color);
  --navbar-menu-border-color: var(--panel-border-color);
  --navbar-menu-background: var(--color-white);
  --navbar-menu-font-color: var(--body-font-color);
  --navbar-menu_hover-background: var(--color-smoke-50);
  /* nav */
  --nav-background: var(--color-light-grayish-green);
  --nav-border-color: var(--color-gray-10);
  --nav-line-height: 1.35;
  --nav-heading-font-color: var(--color-jet-30);
  --nav-muted-color: var(--color-gray-70);
  --nav-panel-divider-color: var(--color-smoke-90);
  --nav-secondary-background: var(--color-smoke-70);
  --nav-components: var(--color-jet-20);
  /* toolbar */
  --toolbar-background: var(--color-smoke-50);
  --toolbar-border-color: var(--color-smoke-50);
  --toolbar-font-color: var(--color-gray-70);
  --toolbar-muted-color: var(--color-gray-10);
  --page-version-menu-background: var(--color-smoke-50);
  --page-version-missing-font-color: var(--color-gray-30);
  /* admonitions */
  --caution-color: #a0439c;
  --caution-on-color: var(--color-white);
  --important-color: #d32f2f;
  --important-on-color: var(--color-white);
  --note-color: #217ee7;
  --note-on-color: var(--color-white);
  --tip-color: #41af46;
  --tip-on-color: var(--color-white);
  --warning-color: #e18114;
  --warning-on-color: var(--color-white);
  /* doc */
  --doc-font-color: var(--color-jet-50);
  --doc-font-size: inherit;
  --doc-font-size--desktop: calc(17 / var(--rem-base) * 1rem);
  --doc-line-height: 1.6;
  --doc-margin: 0 auto;
  --doc-margin--desktop: 0 2rem;
  --doc-min-width--desktop: 70%;
  --heading-font-color: var(--color-jet-80);
  --heading-font-weight: normal;
  --alt-heading-font-weight: var(--body-font-weight-bold);
  --section-divider-color: var(--panel-border-color);
  --link-font-color: var(--Teal-and-Gray-B);
  --link_hover-font-color: var(--Teal-and-Gray-R);
  --nav_item-background-color: var(--color-very-dark-cyan);
  --link_unresolved-font-color: var(--important-color);
  --abstract-background: var(--color-smoke-70);
  --abstract-font-color: var(--color-jet-20);
  --abstract-border-color: var(--panel-border-color);
  --admonition-background: var(--pre-code-background);
  --admonition-label-font-weight: var(--body-font-weight-bold);
  --caption-font-color: var(--color-gray-70);
  --caption-font-weight: var(--body-font-weight-bold);
  --code-background: var(--color-smoke-45);
  --pre-code-background: var(--color-smoke-40);
  --code-font-color: var(--body-font-color);
  --example-background: var(--color-white);
  --example-border-color: var(--color-gray-70);
  --kbd-background: var(--panel-background);
  --kbd-border-color: var(--color-gray-10);
  --pre-background: var(--panel-background);
  --pre-border-color: var(--panel-border-color);
  --pre-annotation-font-color: var(--color-gray-50);
  --quote-background: var(--panel-background);
  --quote-border-color: var(--color-gray-70);
  --quote-font-color: var(--color-gray-70);
  --quote-attribution-font-color: var(--color-gray-30);
  --sidebar-background: var(--color-smoke-90);
  --table-border-color: var(--panel-border-color);
  --table-stripe-background: var(--panel-background);
  --table-footer-background: linear-gradient(to bottom, var(--color-smoke-70) 0%, var(--color-white) 100%);
  /* toc */
  --toc-font-color: var(--nav-muted-color);
  --toc-heading-font-color: var(--doc-font-color);
  --toc-border-color: var(--panel-border-color);
  --toc-line-height: 1.2;
  /* footer */
  --footer-line-height: var(--doc-line-height);
  --footer-background: var(--color-smoke-50);
  --footer-font-color: var(--color-gray-70);
  --footer-link-font-color: var(--color-jet-80);
  /* dimensions and positioning */
  --navbar-height: calc(63 / var(--rem-base) * 1rem);
  --toolbar-height: calc(45 / var(--rem-base) * 1rem);
  --drawer-height: var(--toolbar-height);
  --body-top: var(--navbar-height);
  --body-min-height: calc(100vh - var(--body-top));
  --nav-height: calc(var(--body-min-height) - var(--toolbar-height));
  --nav-height--desktop: var(--body-min-height);
  --nav-panel-menu-height: calc(100% - var(--drawer-height));
  --nav-panel-explore-height: calc(50% + var(--drawer-height));
  --nav-width: calc(270 / var(--rem-base) * 1rem);
  --toc-top: calc(var(--body-top) + var(--toolbar-height));
  --toc-height: calc(100vh - var(--toc-top) - 2.5rem);
  --toc-width: calc(162 / var(--rem-base) * 1rem);
  --toc-width--widescreen: calc(216 / var(--rem-base) * 1rem);
  --doc-max-width: calc(720 / var(--rem-base) * 1rem);
  --doc-max-width--desktop: calc(828 / var(--rem-base) * 1rem);
  /* stacking */
  --z-index-nav: 1;
  --z-index-toolbar: 2;
  --z-index-page-version-menu: 3;
  --z-index-navbar: 4;

  /* algolia search */
  --dropdown-bg-color: var(--color-white);
  --dropdown-header-color: black;
  --dropdown-font-color: black;
  --highlight-grey-color: var(--color-light-grayish-green);
}

@media (prefers-color-scheme: dark) {
  :root {

    /* colors */
    --color-smoke-45: #161b22;
    --color-smoke-40: #33363a75;
    --color-very-dark-blue: var(--code-font-color);

    /* base */
    --panel-background: var(--color-jet-50);

    /* body */
    --body-background: #1f272d;
    --color-gray-70: #9da7b2;
    --link-font-color: #58a6ff;
    --toc-heading-font-color: none;
    --doc-font-color: var(--color-smoke-70);
    --heading-font-color: #c1c1c1;
    --body-font-color: var(--color-gray-70);

    /* code */
    --code-background: #6e768166;
    --code-font-color: #c9d1d0;
    --pre-code-background: var(--color-gray-20);

    /* top navbar */
    --navbar-font-color: var(--color-smoke-30);
    --navbar-background: var(--color-very-dark-cyan);
    --navbar-hover-background: var(--Teal-and-Gray-1);

    /* side nav */
    --nav-background: var(--pre-code-background);
    --nav-secondary-background: var(--color-gray-20);
    --nav-panel-divider-color: var(--color-gray-20);
    --nav-components: var(--color-smoke-90);

    /* navbar dropdown links */
    --navbar-menu-hover-background: var(--Teal-and-Gray-3);
    --navbar-menu-background: var(--body-background);
    --navbar-menu-border-color: var(--Teal-and-Gray-3);
    --navbar-menu-font-color: var(--navbar-font-color);

    /* toolbar */
    --toolbar-background: var(--body-background);

    /* doc */
    --kbd-background: #666;
    --kbd-border-color: #555;
    --pre-background: var(--panel-background);
    --pre-border-color: var(--panel-border-color);
    --sidebar-background: #444;
    --example-background: var(--panel-background);
    --example-border-color: #444;
    --table-footer-background: linear-gradient(to bottom, var(--color-jet-80) 0%, var(--body-background) 100%);

    /* footer */
    --footer-background: var(--body-background);
    --footer-link-font-color: var(--link-font-color);
  }
}
