.doc .codeDoc .sect2 {
  margin-bottom: 3em;
}

.doc .codeDoc .dlist {
  margin-top: 0.2em;
}

.codeDoc .sect2 {
  position: relative;
}

.codeDoc h3 {
  background-color: rgba(128, 128, 128, 0.1);
  padding: 3px;
}

.codeDoc .sect2.sync::after {
  content: "sync";
  position: absolute;
  top: -5px;
  height: auto;
  width: auto;
  right: 0;
  border-radius: 3px;
  background-color: #ff7;
  font-size: 9pt;
  line-height: 9pt;
  padding: 3px 3px 6px 3px;
  color: #333;
  border: 1px solid #333;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.5);
  transform: rotate(-5deg);
}

.codeDoc .sect2.ctor::after {
  content: "constructor";
  position: absolute;
  top: -5px;
  height: auto;
  width: auto;
  right: 0;
  border-radius: 3px;
  background-color: rgb(119, 250, 255);
  font-size: 9pt;
  line-height: 9pt;
  padding: 3px 3px 6px 3px;
  color: #333;
  border: 1px solid #333;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.5);
  transform: rotate(-5deg);
}
