@media (prefers-color-scheme: dark) {
  .nav-menu .title a {
    color: var(--color-smoke-30);
  }
  /* hljs */
  span.hljs-title {
    color: #afa;
  }

  span.hljs-string {
    color: #aaf;
  }

  span.hljs-keyword {
    color: #aaff8d;
  }

  .navbar-dropdown a.navbar-item:hover {
    background: #888;
  }
}
