
#docsearch {
  display: flex;
  align-items: center;
}

.searchbox {
  border: none;
  border-radius: 6px;
  padding-left: 15px;
}

.ds-suggestion:hover {
  background: yellow;
}

@media screen and (max-width: 1024px) {
  .searchbox {
    border-radius: 3px;
    border: 1px solid grey;
    margin-left: 0.7em;
    padding: 0;
    text-indent: 0.5em;
  }

  #algolia-autocomplete-listbox-0.ds-dropdown-menu > * {
    background: none;
  }

  .ds-dataset-1 > * {
    border: none;
  }

  .algolia-autocomplete .algolia-docsearch-suggestion {
    background: var(--dropdown-bg-color);
  }

  .algolia-docsearch-suggestion--subcategory-column {
    display: none;
  }

  .algolia-docsearch-suggestion--subcategory-column .algolia-docsearch-suggestion--subcategory-column-text {
    color: var(--code-font-color);
  }
}

.algolia-docsearch-suggestion--subcategory-column-text {
  color: var(--dropdown-font-color);
}

/* Main dropdown wrapper */
.algolia-autocomplete .ds-dropdown-menu {
  width: 500px;
  background: var(--dropdown-bg-color);
  justify-content: center;
}

#algolia-autocomplete-listbox-0::before {
  background: var(--dropdown-bg-color);
}

/* Main category (eg. Getting Started) */
.algolia-autocomplete .algolia-docsearch-suggestion--category-header {
  color: var(--dropdown-header-color);
}

/* Category (eg. Downloads) */
.algolia-autocomplete .algolia-docsearch-suggestion--subcategory-column {
  color: var(--dropdown-font-color);
}

/* Title (eg. Bootstrap CDN) */
.algolia-autocomplete .algolia-docsearch-suggestion--title {
  font-weight: bold;
  color: var(--dropdown-header-color);
}

/* Description description (eg. Bootstrap currently works...) */
.algolia-autocomplete .algolia-docsearch-suggestion--text {
  font-size: 0.8rem;
  color: var(--dropdown-font-color);
}

/* Highlighted text */
.algolia-autocomplete .algolia-docsearch-suggestion--highlight {
  color: var(--link-font-color);
  padding: 0;
}

div.algolia-docsearch-suggestion--subcategory-column > span::after {
  color: white;
}

#algolia-autocomplete-listbox-0 > *,
.ds-dropdown-menu > * {
  background-color: var(--dropdown-bg-color);
}

div[class^="algolia-docsearch-suggestion"] {
  padding: 0;
}

#algolia-autocomplete-listbox-0 > * {
  opacity: 1;
  background-color: var(--dropdown-bg-color);
  color: var(--code-font-color);
}

.algolia-autocomplete .algolia-docsearch-suggestion {
  background: var(--dropdown-bg-color);
  color: var(--dropdown-font-color);
}

div.algolia-docsearch-suggestion--wrapper > div.algolia-docsearch-suggestion--subcategory-column {
  color: var(--dropdown-font-color);
}

div.algolia-docsearch-suggestion--subcategory-column {
  color: white;
}

div.algolia-docsearch-suggestion--wrapper {
  align-content: center;
}

div.algolia-docsearch-suggestion--wrapper > div.algolia-docsearch-suggestion--content > div.algolia-docsearch-suggestion--title {
  color: var(--dropdown-font-color);
}

div.algolia-docsearch-suggestion--wrapper > div.algolia-docsearch-suggestion--content > div.algolia-docsearch-suggestion--title .algolia-docsearch-suggestion--highlight {
  background: none;
}

.ds-cursor div + .algolia-docsearch-suggestion--content {
  background-color: var(--highlight-grey-color) !important;
}

@media (prefers-color-scheme: dark) {
  :root {
    --dropdown-header-color: var(--code-font-color);
    --dropdown-bg-color: var(--panel-background);
    --dropdown-font-color: var(--code-font-color);
    --highlight-grey-color: #696969;
  }
}
